<template>
<ul class="goodslists">
	<li v-for="item in items">
		<img :src="item.src" />
		<div class="over2">{{item.name}}</div>
		<div class="goodsprice">
			<span>￥</span>{{item.price}}<span>.00</span>
		</div>
	</li>
	<div class="clear"></div>
</ul>
</template>

<script>
export default{
	data(){
		return{
			items:[
			{src:'../../../static/imgs/logo.png',name:'商品名称商品名称商品名称商品名称商品名称商品名称商品名称商品名称商品名称商品名称1',price:'288'},
			{src:'../../../static/imgs/logo.png',name:'商品名称商品名称商品名称商品名称商品名称商品名称商品名称商品名称商品名称商品名称2',price:'299'}
			]
		}
	}
}
</script>

<style scoped>
/*商品列表*/
.goodslists{padding-right: 10px;background: #fff;}
.goodslists li{float: left;width: 50%;text-align: center;margin-right: 10px;margin-bottom: 10px;padding: 5px 0;text-align: left;}
.goodslists li:nth-of-type(2n){margin-right: -10px;}
.goodslists li img{display: block;width: 100%;}
.clear{clear: both;}
.over2{overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 2;}
.goodsprice{color: #FF5773;font-size: .35rem;}
</style>


